<template><section>
<h1>input 输入框</h1>
<p>通过鼠标或键盘输入字符</p>
<h2 id="ji-chu-yong-fa"><a class="header-anchor" href="#ji-chu-yong-fa" aria-hidden="true">¶</a> 基础用法</h2>
<demo-block>
                            <xl-input placeholder="请输入内容"></xl-input>

                            <div slot="desc"><p>基本的使用方式</p>
</div>
                            <div slot="source"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">xl-input</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">'请输入内容'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-input</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="jin-yong-zhuang-tai"><a class="header-anchor" href="#jin-yong-zhuang-tai" aria-hidden="true">¶</a> 禁用状态</h2>
<demo-block>
                            <xl-input placeholder="请输入内容" disabled></xl-input>

                            <div slot="desc"><p>使用<code>disabled</code>禁止输入</p>
</div>
                            <div slot="source"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">xl-input</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">'请输入内容'</span> <span class="hljs-attr">disabled</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-input</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="zhi-du-zhuang-tai"><a class="header-anchor" href="#zhi-du-zhuang-tai" aria-hidden="true">¶</a> 只读状态</h2>
<demo-block>
                            <xl-input placeholder="请输入内容" readonly></xl-input>

                            <div slot="desc"><p>使用<code>readonly</code>只读</p>
</div>
                            <div slot="source"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">xl-input</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">'请输入内容'</span> <span class="hljs-attr">readonly</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-input</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="ke-qing-kong"><a class="header-anchor" href="#ke-qing-kong" aria-hidden="true">¶</a> 可清空</h2>
<demo-block>
                            <xl-input placeholder="请输入内容" clearable=""></xl-input>

                            <div slot="desc"><p>使用<code>clearable</code></p>
</div>
                            <div slot="source"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">xl-input</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">'请输入内容'</span> <span class="hljs-attr">clearable</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-input</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="dai-icon-de-shu-ru-kuang"><a class="header-anchor" href="#dai-icon-de-shu-ru-kuang" aria-hidden="true">¶</a> 带 icon 的输入框</h2>
<demo-block>
                            <xl-input placeholder="请输入内容" suffix-icon="calendar"></xl-input>
<xl-input placeholder="请输入内容" prefix-icon="search"></xl-input>

                            <div slot="desc"><p>使用<code>suffix-icon</code>添加后置图标，使用<code>prefix-icon</code>添加前置图标</p>
</div>
                            <div slot="source"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">xl-input</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">'请输入内容'</span> <span class="hljs-attr">suffix-icon</span>=<span class="hljs-string">'calendar'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-input</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">xl-input</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">'请输入内容'</span> <span class="hljs-attr">prefix-icon</span>=<span class="hljs-string">'search'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-input</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="fu-he-xing-shu-ru-kuang"><a class="header-anchor" href="#fu-he-xing-shu-ru-kuang" aria-hidden="true">¶</a> 复合型输入框</h2>
<demo-block>
                            <xl-input placeholder="请输入内容">
    <span slot="prefix">Http://</span>
    <span slot="suffix">.com</span>
</xl-input>

                            <div slot="desc"><p>使用 slot 名<code>suffix</code>添加后置元素，使用 slot 名<code>prefix</code>添加前置元素</p>
</div>
                            <div slot="source"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">xl-input</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">'请输入内容'</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">span</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">'prefix'</span>&gt;</span>Http://<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">span</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">'suffix'</span>&gt;</span>.com<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">xl-input</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="wen-ben-yu"><a class="header-anchor" href="#wen-ben-yu" aria-hidden="true">¶</a> 文本域</h2>
<demo-block>
                            <xl-input type="textarea" placeholder="请输入内容"></xl-input>

                            <div slot="desc"><p>使用<code>type</code>='textarea'</p>
</div>
                            <div slot="source"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">xl-input</span> <span class="hljs-attr">type</span>=<span class="hljs-string">'textarea'</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">'请输入内容'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-input</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="wen-ben-yu-zi-gua-ying-gao-du"><a class="header-anchor" href="#wen-ben-yu-zi-gua-ying-gao-du" aria-hidden="true">¶</a> 文本域自适应高度</h2>
<demo-block>
                            <xl-input type="textarea" autosize="" placeholder="请输入内容"></xl-input>

                            <div slot="desc"><p>使用<code>autosize</code></p>
</div>
                            <div slot="source"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">xl-input</span> <span class="hljs-attr">type</span>=<span class="hljs-string">'textarea'</span> <span class="hljs-attr">autosize</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">'请输入内容'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-input</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="xian-zhi-ji-xian-gao-du-de-zi-gua-ying-fang-shi"><a class="header-anchor" href="#xian-zhi-ji-xian-gao-du-de-zi-gua-ying-fang-shi" aria-hidden="true">¶</a> 限制极限高度的自适应方式</h2>
<demo-block>
                            <xl-input v-model="minRows" placeholder="最小高度"></xl-input>
<xl-input v-model="maxRows" placeholder="最大高度"></xl-input>
<xl-input type="textarea" :autosize="{minRows:Number(minRows),maxRows:Number(maxRows)}" placeholder="请输入内容"></xl-input>


                            <div slot="desc"><p>将<code>autosize</code>设置成对象，其中包含 <code>minRows</code>和 <code>maxRows</code>，可以只设置其中一个</p>
</div>
                            <div slot="source"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">xl-input</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"minRows"</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"最小高度"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-input</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">xl-input</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"maxRows"</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"最大高度"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-input</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">xl-input</span> <span class="hljs-attr">type</span>=<span class="hljs-string">'textarea'</span> <span class="hljs-attr">:autosize</span>=<span class="hljs-string">"{minRows:Number(minRows),maxRows:Number(maxRows)}"</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">'请输入内容'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-input</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span>{
        data(){
            <span class="hljs-keyword">return</span> {
                <span class="hljs-attr">minRows</span>:<span class="hljs-string">'0'</span>,
                <span class="hljs-attr">maxRows</span>:<span class="hljs-string">'5'</span>
            }
        }
    }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="input-shu-xing"><a class="header-anchor" href="#input-shu-xing" aria-hidden="true">¶</a> input 属性</h2>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>type</td>
<td>类型</td>
<td>string</td>
<td>text / password /textarea / url / email / date</td>
<td>text</td>
</tr>
<tr>
<td>value</td>
<td>输入参数，支持 v-model 绑定</td>
<td>string</td>
<td>—</td>
<td>-</td>
</tr>
<tr>
<td>prefix-icon</td>
<td>前置图标</td>
<td>string</td>
<td>—</td>
<td>-</td>
</tr>
<tr>
<td>suffix-icon</td>
<td>后置图标</td>
<td>string</td>
<td>—</td>
<td>-</td>
</tr>
<tr>
<td>autosize</td>
<td>针对 textarea 文本域的自适应高度,设置成对象时格式为{minRows:1,maxRows:10}</td>
<td>boolean/object</td>
<td>-</td>
<td>false</td>
</tr>
<tr>
<td>clearable</td>
<td>可清空输入框</td>
<td>boolean</td>
<td>-</td>
<td>false</td>
</tr>
<tr>
<td>disabled</td>
<td>禁用文本框</td>
<td>boolean</td>
<td>-</td>
<td>false</td>
</tr>
<tr>
<td>readonly</td>
<td>只读文本框</td>
<td>boolean</td>
<td>-</td>
<td>false</td>
</tr>
</tbody>
</table>
<h2 id="input-fang-fa"><a class="header-anchor" href="#input-fang-fa" aria-hidden="true">¶</a> input 方法</h2>
<table class="table"><thead>
<tr>
<th>方法名</th>
<th>说明</th>
<th>参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>focus</td>
<td>使 input 获取焦点</td>
<td>-</td>
</tr>
<tr>
<td>blur</td>
<td>使 input 失去焦点</td>
<td>-</td>
</tr>
</tbody>
</table>
<h2 id="input-shi-jian"><a class="header-anchor" href="#input-shi-jian" aria-hidden="true">¶</a> input 事件</h2>
<table class="table"><thead>
<tr>
<th>事件名</th>
<th>说明</th>
<th>参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>on-input</td>
<td>输入事件</td>
<td>输入框值</td>
</tr>
<tr>
<td>on-focus</td>
<td>获得焦点事件</td>
<td>原生事件对象 e</td>
</tr>
<tr>
<td>on-blur</td>
<td>获得失去焦点事件</td>
<td>原生事件对象 e</td>
</tr>
<tr>
<td>on-icon-click</td>
<td>图标点击事件</td>
<td>-</td>
</tr>
</tbody>
</table>
</section></template>
<style>
    .content .xl-input{
        width: 20em;
    }

    .content .xl-input + .xl-input{
        margin-top:1em;
    }
</style>
<script>
    export default{
        data(){
            return {
                minRows:'0',
                maxRows:'5'
            }
        }
    }
</script>


